  margin-bottom: 10px;
  font-size: 0.9375rem;
  font-family: "OpenSansLight", sans-serif;
}

.footer_contacts li span {
  position: relative;
}

.footer_contacts li a {
  color: #fff;
  text-decoration: none;
  display: block;
  line-height: 1.9000;
  position: relative;
}

.footer_contacts li span:before,
.footer_contacts li a[href^="mailto:"]:before,
.footer_contacts li a[href^="tell:"]:first-child:before {
  position: absolute;
  font-family: "FontAwesome";
  color: #fff;
}

.footer_contacts li span:before {
  top: 2px;
  left: -18px;
  font-size: 0.9375rem;
  content: '\f041';
}

.footer_contacts li a[href^="mailto:"]:before {
  top: 4px;
  left: -20px;
  font-size: 0.7rem;
  content: '\f0e0';
}

.footer_contacts li a[href^="tell:"]:first-child:before {
  top: 0;
  left: -18px;
  font-size: 1.200rem;
  content: '\f10b';
}

